import React from "react";
import styled, { useTheme } from "styled-components";
import whiteImage from "@images/wip_white.png";
import darkImage from "@images/wip_black.png";
import { useTranslation } from "gatsby-plugin-react-i18next";

const Layout = styled.div.withConfig({ componentId: "WorkInProgress_Layout" })`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${({ theme }) => theme.color};
  font-weight: 600;
  height: 100%;

  p {
    margin-top: 4px;
  }
`;

export default function WorkInProgress() {
  const theme = useTheme();
  const currentTheme = theme?.type || "dark";
  const isDarkTheme = currentTheme === "dark";
  const { t } = useTranslation();
  return (
    <Layout>
      <img
        src={isDarkTheme ? whiteImage : darkImage}
        alt="still working on it"
      />
      <p>{t("workInProgress")}</p>
    </Layout>
  );
}
